* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
.app-container {
  display: flex;
  height: calc(100vh - 50px);
  overflow-y: scroll;
 
}
ul {
  list-style: none;
}

/*Topbar Section */

.topbarContainer {
  height: 50px;
  background-color: #5e5eed;
  display: flex;
  align-items: center;
}
.topbar-title {
  flex: 2;
}

.topbar-title h2 a {
  margin-left: 20px;
  font-size: 30px;
  cursor: pointer;
  color: white;
  text-decoration: none;
}
.topbar-title h2:hover {
  opacity: .8;
}
.topbar-middle {
  flex: 7.5;
  display: flex;
  align-items: center;
}
.topbar-middle .topbar-searchIcon {
  position: relative;
  left: 30px;

}
.topbar-middle .topbar-search{
 width: 70%;
 height: 40px;
 border-radius: 30px;
 text-indent: 5%;
}
.topbar-middle .topbar-search:focus {
  outline: none;
}
.topbarContainer ul li {
  display: inline-block;
  margin-left: 8px;
  color: white;
  font-weight: 500;
}
.topbarContainer ul li a {
  text-decoration: none;
  color: white;
}
.topbarContainer ul li a:hover {
  opacity: .8;
}
.topbar-right {
  flex: 2.5;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.topbar-right span {
  position: relative;
  top: -15px;
  left: -10px;
  background-color: red;
  border-radius: 50%;
  color: white;
  text-align: center;
  padding: 5px;
}
.topbar-right .usernameImg img {
  width: 42px;
  height: 42px;
  object-fit: cover;
  border-radius: 50%;
  border:2px solid white
}
@media (max-width:767px) {
  .app-container {
    height: calc(100vh - 150px);
  }
  .topbarContainer {
    height: 150px;
    flex-direction: column;
  }
  .topbar-title {
    flex: 4;
  }
  .topbar-middle {
    flex: 4;
    width: 100%;
  }
  .topbar-middle .topbar-search{
    width: 50%;
    text-indent: 20px;
    font-size: 10px;
   }

   .topbar-right {
    flex: 4;
    font-size: 20px;
  }
   
   .topbar-right .usernameImg img {
    position: absolute;
    top: 100px;
    left: 85%;
  }

}

/*Sidebar Section */
.sidbarOpenIcon {
  display: none;
}
.closeSidebar {
  display: none;
}
.sidebar-section {
  display: flex;
  flex: 2.5;
  justify-content: flex-start;
  flex-direction: column;
  overflow-y: scroll;
  transition: 1s;
}
::-webkit-scrollbar {
  width: 2px;
}
::-webkit-scrollbar-track {
  background-color: white;
}
::-webkit-scrollbar-thumb {
  background-color: #9595dd;

}
.sidebar-section .ctg-section {
  display: flex;
  flex-direction: column;
}
.sidebar-section .ctg-item {
  display: flex;
  align-items: center;
  margin: 20px;
}
.sidebar-section .ctg-item:hover {
  opacity: .8;
}

.sidebar-section .ctg-item a {
  margin-left: 20px;
  padding: 0;
  text-decoration: none;
  color: black;
  font-weight: bold;
  text-align: left;
}
.sidebar-section .friendList-section {
  display: flex;
  flex-direction: column;
}
.friendList-section .friend {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: 500;
}
.friendList-section .friend:hover {
  opacity: .8;
}
.friend img {
  margin: 20px;
  width: 42px;
  height: 42px;
  border-radius: 80%;
  object-fit: cover;

}
@media (max-width:767px) {
  .sidbarOpenIcon {
    display: block;
    cursor: pointer;
    color: white;
    position: absolute;
    left: 5px;

  }
  .closeSidebar {
    display: block;
    position: relative;
    left:85%;
    top: 20px;
    cursor: pointer;
    color: black;
  }
  .sidebar-section {
    display: none;
    position: absolute;
    background-color: white;
    z-index: 999;
    width: 100%;
    margin-left: 10px;
    font-size: 22px;
    overflow-x: hidden;
  }
}
/*Feed Section */
.feed-section{
  flex: 7;
  overflow-y: scroll;
  overflow-x: hidden;
}

.share-container {
  width: 90%;
  height: 200px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  margin: 30px auto;
  box-shadow: -8px 6px 10px 3px rgba(212,210,210,0.75);
  -webkit-box-shadow: -8px 6px 10px 3px rgba(212,210,210,0.75);
  -moz-box-shadow: -8px 6px 10px 3px rgba(212,210,210,0.75);
}
.share-container .input-section {
  display: flex;
  align-items: center;
  margin: 10px 0 0 10px;
}
.share-container img {
  width: 72px;
  height: 72px;
  object-fit: cover;
  border-radius: 50%;
}
.share-container input {
  width: 100%;
  height: 50px;
  border: none;
  text-indent: 20px;
  font-size: 15px;
}
.share-container input:focus {
  outline: none;
}
.share-container hr {
  margin: 30px 0;
  color: #9595dd;
}
.icons-section {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.share-container .icons-section li {
  display: inline-block;
  margin-right: 25px;
  cursor: pointer;
  font-weight: bold;
}
.share-container .icons-section li span {
  position: relative;
  top: 6px;
}
.share-container .icons-section li:hover,.share-container .icons-section button:hover {
  opacity: .8;
}
.share-container .icons-section button {
  background-color: green;
  color: white;
  padding: 13px;
  border-radius: 15px;
  border: none;
  font-weight: bold;
  cursor: pointer;
}
.post-container {
  width: 90%;
  padding: 20px 0px;
  display: flex;
  flex-direction: column;
  margin: 30px auto;
  box-shadow: -4px 4px 9px -1px rgba(171,171,171,0.75);
  -webkit-box-shadow: -4px 4px 9px -1px rgba(171,171,171,0.75);
  -moz-box-shadow: -4px 4px 9px -1px rgba(171,171,171,0.75);
}
.post-container .post-info {
  width: 100%;
  display: flex;
  align-items: center;
}
.post-container .post-info h4 {
  margin-left: 20px;
}
.post-container .post-info img {
  width:42px;
  height: 42px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #5e5eed
}
.post-info h4 span {
  opacity: .7;
  font-size: 13px;
  font-weight: 500;
}
.post-container p {
  margin-bottom: 20px;
  text-indent: 20px;
}
.post-container img {
  max-width: 100%;
  max-height: 80%;
}
.post-info {
  margin: 20px 0 20px 20px;
  display: flex;
  align-items: center;
}
.post-info .icon {
  cursor: pointer;
}
.post-info .icon:hover {
  opacity: .8;
}
.post-info span {
  opacity: .8;
  margin-left: 20px;
}
.post-container .comments {
  margin-left: 20px;
  cursor: pointer;
}
@media (max-width:767px) {
  .feed-section .share-container {
    height: 50%;
    width: 100%;
  }
  .share-container .icons-section li {
    margin-right: 5px;
    font-size: 8px;
  }
  .feed-section .post-container {
 
    height: 50%;

  }
  .post-container img {
    max-width: 100%;
    max-height: 80%;
  }
}
/*Right Section */
.right-section {
  flex: 2.5;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-top: 50px;
  overflow-y: scroll;

}
.right-section p {
  text-align: left;
  margin-left: 10px;
}
.right-section span {
  font-weight: bold;
}
.right-section .add-img {
  width: 90%;
  height: 300px;
  object-fit: contain;
  margin: 0 auto;
}
.right-section .online-Friends li{
  display: flex;
  align-items: center;

}
.right-section  h4 {
  margin-left: 10px;
}
.right-section .online-Friends img {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  margin: 15px;
  object-fit: cover;
}
@media (max-width:767px) {
  .right-section {
    display: none;
  }
}

/* Profile Section */
.profile-section {
  display: flex;
  align-items: center;
  flex-direction: column;
  flex: 9.5;
  overflow-y: scroll;
  overflow-x: hidden;
}
.profile-section .header {
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.profile-section .header .coverPic {
  width: 100%;
  height: 70%;
  background-size: cover;
  background-size: 100% 100%;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}
.profile-section  .profile-img {
  width: 220px;
  height: 220px;
  object-fit: cover;
  border-radius: 50%;
  position: relative;
  top: 50px;
  border: 5px solid #5e5eed;
}

.profile-section  h2 {
  margin-top: 55px;
  font-size: 30px;
  font-weight: 900;
}


.profile-section  p {
  margin: 5px;
  opacity: .8;
}

.profile-page {
  width: 100%;
  height:40%;
  overflow: visible;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.profile-page .main {
  flex: 9;
  display: flex;
  flex-direction: column;

}
.profile-page .info {
  flex: 3;
  background-color: #dddddd;
  border-radius: 25px;
  width: 90%;
  height: 350px;
}
.profile-page .info h2 {
  margin-bottom: 50px;
  text-indent: 12px;
}
.profile-page .info li {
  margin-bottom: 15px;
  margin-left: 10px;
}
.profile-page .info span {
  font-weight: bold;
}
@media (max-width:767px) {
  .profile-page {
    flex-direction: column;
    align-items: center;
  }
  .profile-page .main {
    order: 2;
  
  }
  .profile-page .info {
    order: 1;
    margin-top: 50px;
  }
 .profile-page .info ul  {
    margin-left: 30px;
  }
}